/*
 * Colors
 */
.TopBarComp {
  display: flex;
  align-content: center;
  justify-content: space-between;
  height: 140px; }
  @media (max-width: 768px) {
    .TopBarComp {
      padding: 1rem;
      align-items: center;
      flex-direction: column;
      height: auto;
      box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1); } }
  .TopBarComp > .logo {
    width: 140px; }
    @media (max-width: 768px) {
      .TopBarComp > .logo {
        margin: 1rem 0; } }
  .TopBarComp > .menu {
    display: flex;
    align-items: center; }
    .TopBarComp > .menu > ul {
      margin: 0;
      padding: 0;
      display: flex;
      align-items: center;
      list-style: none; }
      .TopBarComp > .menu > ul > li {
        margin: 0 2rem; }
        @media (max-width: 768px) {
          .TopBarComp > .menu > ul > li {
            padding: 1rem 0;
            text-align: center;
            font-size: 1.4rem; } }
        .TopBarComp > .menu > ul > li:last-child {
          margin-right: 0; }

