/*
 * Colors
 */
.CardsComp {
  background: url(imgs/bg-cardlist.png) #f5f5f5;
  background-size: 77px auto;
  margin-top: 3rem;
  padding: 1rem 0;
  position: relative; }
  .CardsComp > .search {
    display: none; }
  .CardsComp .cards-list {
    display: flex;
    flex-wrap: wrap;
    padding-top: 5rem;
    justify-content: space-between; }
    @media (max-width: 768px) {
      .CardsComp .cards-list {
        padding-left: 1rem;
        padding-right: 1rem; } }
    .CardsComp .cards-list > .card {
      position: relative;
      max-width: 23%;
      min-width: 23%;
      flex: 1 0 23%;
      margin: 0 0 5rem;
      min-height: 250px;
      padding: 1rem;
      background: #fff;
      border-radius: 5px;
      box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1); }
      @media (min-width: 769px) and (max-width: 1024px), (min-width: 1025px) {
        .CardsComp .cards-list > .card {
          transition: all 0.2s ease-in-out; } }
      @media (min-width: 769px) and (max-width: 1024px) {
        .CardsComp .cards-list > .card {
          max-width: 30%;
          min-width: 30%;
          flex: 1 0 30%; } }
      @media (max-width: 768px) {
        .CardsComp .cards-list > .card {
          max-width: 100%;
          min-width: 100%;
          flex: 1 0 100%;
          margin: 0 0 2rem; } }
      @media (min-width: 769px) and (max-width: 1024px), (min-width: 1025px) {
        .CardsComp .cards-list > .card:hover {
          transform: translate3d(0, -10px, 0);
          box-shadow: 0 20px 40px rgba(0, 0, 0, 0.05); } }
      .CardsComp .cards-list > .card > .image {
        background: #f5f5f5;
        border-radius: 3px;
        min-height: 150px; }
      .CardsComp .cards-list > .card > .title {
        color: #3cb054;
        margin: 1rem 0; }
      .CardsComp .cards-list > .card > .description {
        color: #555;
        font-size: 1.3rem;
        line-height: 2rem; }
      .CardsComp .cards-list > .card > .action-buttons {
        display: flex;
        justify-content: space-between; }
        .CardsComp .cards-list > .card > .action-buttons .btn {
          padding: 0.8rem 2.2rem; }
        .CardsComp .cards-list > .card > .action-buttons .btn + .btn {
          margin-top: 0; }
      .CardsComp .cards-list > .card > .donation-popup {
        position: absolute;
        margin: 1rem;
        width: calc(100% - 2rem);
        height: calc(100% - 2rem);
        top: 0;
        left: 0;
        overflow: auto;
        padding: 1rem;
        border-radius: 3px;
        background: #f5f5f5;
        opacity: 0;
        transform: scale(0.95);
        transition: all 0.3s ease-in-out;
        pointer-events: none; }
        .CardsComp .cards-list > .card > .donation-popup.on {
          pointer-events: all;
          opacity: 1;
          transform: scale(1); }
        .CardsComp .cards-list > .card > .donation-popup > .close {
          width: 16px;
          height: 16px;
          position: absolute;
          right: 10px;
          top: 10px; }
        .CardsComp .cards-list > .card > .donation-popup > h1 {
          margin: 0;
          padding: 0rem 50px 1rem 0;
          font-size: 1.5rem;
          border-bottom: 1px solid rgba(153, 153, 153, 0.2); }
        .CardsComp .cards-list > .card > .donation-popup > h2 {
          text-align: center;
          margin-top: 4rem;
          opacity: 0.3;
          font-weight: normal;
          font-size: 1.5rem; }
        .CardsComp .cards-list > .card > .donation-popup .title {
          font-size: 1.3rem;
          margin-top: 1rem; }
        .CardsComp .cards-list > .card > .donation-popup .address {
          font-size: 1.2rem;
          color: #3cb054;
          font-weight: bold;
          margin: 0.5rem 0 2rem; }

