html, body {
  font-size: 10px;
  font-family: "Lato", sans-serif;
  margin: 0;
  padding: 0; }

body {
  background: #fff;
  font-size: 1.5rem;
  display: flex;
  min-height: 100vh;
  flex-direction: column; }
  body.no-scroll {
    overflow: hidden; }

hr {
  border: none;
  border-bottom: 1px solid rgba(153, 153, 153, 0.2);
  margin: 1rem 0; }

a {
  text-decoration: none;
  color: #3cb054;
  transition: color 0.2s linear; }
  a:hover {
    color: #226430; }

main {
  flex: 1;
  transform: translate3d(0, 0, 0);
  transition: transform 0.4s cubic-bezier(0.17, 0.67, 0.42, 0.94); }
  main.off {
    transform: translate3d(-100px, 0, 0); }

.primary {
  color: #3cb054; }

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem; }
  @media (max-width: 768px) {
    .container {
      padding: 0; } }

.hero {
  min-height: 100px;
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  margin-bottom: 9rem; }
  @media (max-width: 768px) {
    .hero {
      margin: 5rem 0; } }
  .hero h1 {
    margin: 0;
    font-size: 2.4rem; }
  .hero h2 {
    margin: 1rem 0 0 0;
    font-size: 2rem;
    font-weight: normal; }

.btn {
  display: block;
  border-radius: 3px;
  border: none;
  background: #3cb054;
  border: 1px solid #3cb054;
  color: #fff;
  padding: 1rem 2rem;
  text-align: center;
  font-size: 1.3rem; }
  .btn.outline {
    background: transparent;
    border-color: #3cb054;
    color: #3cb054; }
  .btn + .btn {
    margin-top: 0.5rem; }
  .btn:not(.outline):hover {
    color: #fff;
    background: #2f8a42;
    border-color: #2f8a42; }

.hero-img {
  width: 413px;
  margin-bottom: 3rem; }
  @media (max-width: 768px) {
    .hero-img {
      width: 80%; } }

