/*
 * Colors
 */
.SideBarComp {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden; }
  .SideBarComp > .overlay {
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(85, 85, 85, 0.7);
    opacity: 0;
    opacity: 0;
    transition: opacity 0.4s linear; }
    .SideBarComp > .overlay.on {
      opacity: 1; }
  .SideBarComp .sidebar {
    position: fixed;
    z-index: 2;
    top: 0;
    right: 0;
    width: 400px;
    height: 100vh;
    overflow: auto;
    padding: 2rem;
    background: #fff;
    transition: transform 0.4s cubic-bezier(0.17, 0.67, 0.42, 0.94);
    transform: translate3d(100%, 0, 0); }
    @media (max-width: 768px) {
      .SideBarComp .sidebar {
        width: 300px; } }
    .SideBarComp .sidebar.on {
      transform: translate3d(0, 0, 0); }
    .SideBarComp .sidebar > .title {
      font-weight: normal;
      margin: 0;
      color: #3cb054; }
    .SideBarComp .sidebar > .description {
      margin: 2rem 0;
      color: #555;
      font-size: 1.5rem;
      line-height: 2.2rem; }

