/*
 * Colors
 */
.FooterComp {
  display: flex;
  align-content: center;
  justify-content: space-between;
  align-items: row;
  padding: 2rem 0; }
  @media (max-width: 768px) {
    .FooterComp {
      flex-direction: column;
      align-items: center; } }
  .FooterComp > .logo {
    display: flex;
    align-items: center;
    width: 100px; }
    .FooterComp > .logo img {
      width: 100%; }
    @media (max-width: 768px) {
      .FooterComp > .logo {
        margin: 1rem 0; } }
  .FooterComp > .menu {
    display: flex;
    align-items: center; }
    .FooterComp > .menu > ul {
      margin: 0;
      padding: 0;
      display: flex;
      align-items: center;
      list-style: none; }
      @media (max-width: 768px) {
        .FooterComp > .menu > ul {
          flex-direction: column; } }
      .FooterComp > .menu > ul > li {
        margin: 0 2rem;
        font-size: 1.3rem; }
        @media (max-width: 768px) {
          .FooterComp > .menu > ul > li {
            padding: 1rem 0;
            text-align: center;
            font-size: 1.4rem;
            margin: 0; } }
        .FooterComp > .menu > ul > li:last-child {
          margin-right: 0; }

